// app/components/Sidebar.tsx
import { Home, MessageCircle, Users, Workflow, Settings } from "lucide-react";
import Link from "next/link";

const menu = [
  { icon: <Home size={22} />, label: "Início", href: "/pages/painel" },
  { icon: <MessageCircle size={22} />, label: "WhatsApp", href: "/pages/painel/whatsapp" },
  { icon: <Users size={22} />, label: "CRM", href: "/pages/painel/crm" },
  { icon: <Workflow size={22} />, label: "Fluxograma", href: "/pages/painel/fluxograma" },
  { icon: <Settings size={22} />, label: "Configurações", href: "/pages/painel/config" },
];

export default function Sidebar() {
  return (
    <aside className="h-screen w-48 bg-white shadow-lg flex flex-col py-8 px-4">
      <div className="text-2xl font-extrabold mb-12 text-center">
        <span className="text-blue-600">BG</span>Solutions
      </div>
      <nav className="flex flex-col gap-2">
        {menu.map((item, i) => (
          <Link
            href={item.href}
            key={i}
            className="flex items-center gap-3 px-3 py-2 rounded-lg hover:bg-blue-100 font-medium text-gray-800 transition"
          >
            {item.icon}
            <span className="text-base">{item.label}</span>
          </Link>
        ))}
      </nav>
    </aside>
  );
}
