'use client';
import { useState } from "react";
import Header from "../components/Header";
import Sidebar from "../components/sidebar";

export default function Painel() {
  const [page, setPage] = useState('dashboard');

  return (
    <div className="min-h-screen flex flex-col">
      {/* Header fixo */}
      <Header user="Gabriel" onLogout={() => { /* lógica de logout */ }} />

      <div className="flex flex-1">
        {/* Sidebar lateral */}
        <Sidebar current={page} onSelect={setPage} />

        {/* Conteúdo principal */}
        <main className="flex-1 bg-slate-50 p-10 overflow-auto">
          {page === 'dashboard' && (
            <div>
              <h1 className="text-3xl font-bold mb-2">Bem-vindo ao Painel BG Solutions</h1>
              <p className="text-gray-700">Gerencie sua automação, clientes e fluxos aqui.</p>
              {/* Adicione cards, gráficos, KPIs etc aqui depois */}
            </div>
          )}
          {page === 'qrcode' && (
            <div>
              <h2 className="text-2xl font-bold mb-3">Conectar WhatsApp</h2>
              <div className="bg-white shadow rounded-lg p-8 text-center">
                <span className="text-gray-400 block mb-2">Seu QR Code aparecerá aqui</span>
                <div className="h-56 w-56 mx-auto bg-slate-200 rounded-lg flex items-center justify-center">
                  {/* Coloque o componente de QR Code real depois */}
                  <span className="text-gray-400 text-3xl">QR</span>
                </div>
                <p className="mt-4 text-gray-600 text-sm">Abra o WhatsApp {'>'} Dispositivos conectados {'>'} Escanear QR Code.</p>
              </div>
            </div>
          )}
          {page === 'whatsapp' && (
            <div>
              <h2 className="text-2xl font-bold mb-3">WhatsApp Web</h2>
              <p className="text-gray-600">Interface em construção (em breve: lista de conversas, envio/recebimento, IA...)</p>
            </div>
          )}
          {page === 'crm' && (
            <div>
              <h2 className="text-2xl font-bold mb-3">CRM de Clientes</h2>
              <p className="text-gray-600">Cadastro e gestão de clientes, leads e histórico de conversas.</p>
            </div>
          )}
          {page === 'fluxograma' && (
            <div>
              <h2 className="text-2xl font-bold mb-3">Editor de Fluxogramas</h2>
              <p className="text-gray-600">Construa fluxos de automação visualmente. (React Flow, Drag-and-drop...)</p>
            </div>
          )}
          {page === 'config' && (
            <div>
              <h2 className="text-2xl font-bold mb-3">Configurações</h2>
              <p className="text-gray-600">Ajuste integrações, preferências e dados da conta.</p>
            </div>
          )}
        </main>
      </div>
    </div>
  );
}
